import _ from 'lodash'
import uuidv4 from '@/utils/uuid'
import packageJson from '@/static/temp/package.json'
export default {
  name: 'EditorPanelModules',
  data () {
    return {
      list: packageJson,
      keyword: '',
      current: null
    }
  },
  computed: {
  },
  mounted () {
  },
  methods: {
    // 搜索方法
    searchLocal: _.debounce(function () {
      const list = []
      for (const item of packageJson) {
        let exist = false
        if (item.name.includes(this.keyword)) { exist = true }
        if (item.keywords && item.keywords.includes(this.keyword)) { exist = true }
        if (item.description && item.description.includes(this.keyword)) { exist = true }
        if (exist) { list.push(item) }
      }
      this.list = this.keyword ? list : packageJson
    }, 100),
    // 添加组件到页面
    addToPageHandle (item) {
      if (!item.name) {
        return
      }

      const c = item
      // 创建一个新的id
      const key = uuidv4()
      // 精简一下数据
      // this.$store.commit('news/addComponents', { key, name: c.name, description: c.description, data: {} })
      this.$store.commit('news/addComponents', { key, name: c.name, description: c.description, data: undefined })
      this.$message({
        type: 'success',
        message: this.$t('The selected module has been successfully added to the page')
      })
      this.$store.commit('side/setSideRightTabActiveName', 'module-option')
      this.$store.commit('side/setSideLeftTabActiveName', 'second')
      this.$store.commit('news/setCurrentComponentKey', key)
    },
    // 点击激活组件
    activeHandle (item) {
      this.current = item
    },
    // 清除组件激活状态
    clearActiveHandle (item) {
      if (this.current === item) {
        this.current = null
      }
    }
  }
}
