import _ from 'lodash'

// 默认数据
const defaultData = {
  image: {
    src: '',
    width: undefined,
    height: undefined,
    alt: 'news image'
  },
  text: {
    value: '',
    style: {}
  },
  textPicSpace: 1
}
export default {
  // 组件名称
  name: 'MdNewsImageLarge',
  props: {
    // data 为当前模块的存储数据
    data: {
      type: Object,
      default: () => {
        return defaultData
      }
    }
  },
  data () {
    return {
    }
  },
  async fetch () {
    // 这个请求，在SSG阶段执行
  },
  computed: {
    finalData () {
      return _.defaults(this.data, defaultData)
    },
    SpaceStyle () {
      return {
        gap: this.finalData.textPicSpace + 'em'
      }
    }
  },
  watch: {

  },
  mounted () {
    // this.ajax()
  },
  methods: {}
}
