export default {
  name: 'MdStyleTextOutput',
  props: {
    data: {
      type: Object,
      default () {
        return {
          value: '',
          style: {}
        }
      }
    }
  },
  methods: {
    replaceBr (content) {
      return ('<p>' + content.split('\n').join('</p><p>') + '</p>').replaceAll('<p></p>', '<p>&nbsp;</p>')
    }
  }
}
