/**
 * 当前新闻数据
 */
export const newsData = {
  // 分类id
  categoryId: null,
  // 新闻id
  id: null,
  // 新闻内容
  newsLangs: {},
  // ?
  pageType: null,
  // 发布时间
  publishTime: new Date(),
  // 是否推荐
  recommend: 0,
  // ?
  remark: null,
  // 排序
  sort: 0,
  // 发表状态
  status: 1
}
export const state = () => {
  // 填充默认的newslangs
  const newsLangs = {}
  if (process.env.contentLangs) {
    process.env.contentLangs.forEach((lang) => {
      if (lang.code) {
        newsLangs[lang.code] = newsLangs[lang.code] || JSON.parse(JSON.stringify(newsLang))
      }
    })
  }

  newsData.newsLangs = newsLangs

  return {
    // 修改是否已经保存
    isChangeSaved: true,
    // 当前选中的组件
    currentComponentKey: '',
    // 内容语言
    contentLang: 'en',
    // 新闻数据
    data: newsData
  }
}

export const mutations = {
  setSate (state, val) {
    state = val
  },
  setIsChangeSaved (state, val) {
    state.isChangeSaved = val
  },
  // 保存新闻数据
  setNewsData (state, val) {
    state.data = val
  },
  // 设置新闻id
  setNewsId (state, id) {
    state.data.id = id
  },
  // 设置内容语言
  setContentLang (state, val) {
    if (state.contentLang !== val) {
      // 如果修改了语言，那么置空当前组件的key
      state.currentComponentKey = ''
    }
    state.contentLang = val
  },
  // 设置NewsLangs除了内容以外的数据
  setNewsLangsExcludeContent (state, val) {
    for (const lang in val) {
      if (Object.hasOwnProperty.call(val, lang)) {
        const element = val[lang]
        for (const key in element) {
          if (key !== 'content' && Object.hasOwnProperty.call(element, key)) {
            const value = element[key]
            state.data.newsLangs[lang] = state.data.newsLangs[lang] || {}
            state.data.newsLangs[lang][key] = value
          }
        }
      }
    }
  },
  // 从某语言copy全部组件数据
  copyComponentsForLang (state, langCode) {
    if (state.data.newsLangs?.[langCode]?.components) {
      const copy = JSON.parse(JSON.stringify(state.data.newsLangs[langCode].components))
      state.data.newsLangs[state.contentLang] = state.data.newsLangs[state.contentLang] || {}
      state.data.newsLangs[state.contentLang].components = copy
    }
  },
  // copy指定当前选中组件数据到其他语言的相同key的组件，并指定复制那些数据
  copyComponentsDataToLang (state, payload) {
    const { sourceLang, sourceKey, targetLangs, options } = payload
    if (sourceLang && sourceKey && targetLangs?.length && options?.length) {
      const sourceComponentData = state.data.newsLangs?.[sourceLang]?.components?.find(x => x.key === sourceKey)?.data
      if (sourceComponentData) {
        targetLangs.forEach((lang) => {
          const targetComponent = state.data.newsLangs?.[lang]?.components?.find(x => x.key === sourceKey)
          if (targetComponent) {
            options.forEach((opt) => {
              targetComponent.data = targetComponent.data || {}
              if (sourceComponentData?.[opt] !== undefined) {
                targetComponent.data[opt] = JSON.parse(JSON.stringify(sourceComponentData?.[opt]))
              } else {
                targetComponent.data[opt] = undefined
              }
            })
          }
        })
      }
    }
  },
  setComponents (state, val) {
    state.data.newsLangs[state.contentLang] = state.data.newsLangs[state.contentLang] || {}
    state.data.newsLangs[state.contentLang].components = val
  },
  // 设置给定key的组件的数据
  setComponentData (state, val) {
    const { key, data } = val

    state.data.newsLangs[state.contentLang] = state.data.newsLangs[state.contentLang] || {}
    state.data.newsLangs[state.contentLang].components = state.data.newsLangs[state.contentLang].components || []
    const components = state.data.newsLangs[state.contentLang].components

    const componentsCopy = JSON.parse(JSON.stringify(components))
    for (let i = 0; i < componentsCopy.length; i++) {
      if (componentsCopy[i].key === key && state.currentComponentKey === key) {
        // componentsCopy[i].data = data

        componentsCopy[i].data = data
      }
    }
    state.data.newsLangs[state.contentLang].components = componentsCopy
  },
  // 设置当前选中的组件的key
  setCurrentComponentKey (state, val) {
    state.currentComponentKey = val
  },
  // 添加一个组件到当前语言内容
  addComponents (state, val) {
    state.data.newsLangs[state.contentLang] = state.data.newsLangs[state.contentLang] || {}
    state.data.newsLangs[state.contentLang].components = state.data.newsLangs[state.contentLang].components || []
    state.data.newsLangs[state.contentLang].components.push(val)
    // 如果数据改变了，那么要提示保存
    state.isChangeSaved = false
  },
  // 删除一个组件从当前语言内容
  removeComponents (state, key) {
    state.data.newsLangs[state.contentLang] = state.data.newsLangs[state.contentLang] || {}
    state.data.newsLangs[state.contentLang].components = state.data.newsLangs[state.contentLang].components || []
    const components = state.data.newsLangs[state.contentLang].components
    if (components) {
      state.data.newsLangs[state.contentLang].components = components.filter(x => x.key !== key)
      if (key === state.currentComponentKey) {
        // 如果当前选中的组件被删除，那么置空当前组件的key
        state.currentComponentKey = ''
      }
      /* let index = null
      for (let i = 0; i < components.length; i++) {
        if (components[i].key === key) {
          index = i
          break
        }
      }
      components.splice(index, 1) */
      // 如果数据改变了，那么要提示保存
      state.isChangeSaved = false
    }
  },
  // 上移一个组件从当前语言内容
  moveUpComponents (state, key) {
    state.data.newsLangs[state.contentLang] = state.data.newsLangs[state.contentLang] || {}
    state.data.newsLangs[state.contentLang].components = state.data.newsLangs[state.contentLang].components || []
    const components = state.data.newsLangs[state.contentLang].components

    if (components) {
      let index = null
      for (let i = 0; i < components.length; i++) {
        if (components[i].key === key) {
          index = i
          break
        }
      }
      if (index > 0) {
        components[index] = components.splice(index - 1, 1, components[index])[0]
      }
      // 如果数据改变了，那么要提示保存
      state.isChangeSaved = false
    }
  },
  // 下移一个组件从当前语言内容
  moveDownComponents (state, key) {
    state.data.newsLangs[state.contentLang] = state.data.newsLangs[state.contentLang] || {}
    state.data.newsLangs[state.contentLang].components = state.data.newsLangs[state.contentLang].components || []
    const components = state.data.newsLangs[state.contentLang].components

    if (components) {
      let index = null
      for (let i = 0; i < components.length; i++) {
        if (components[i].key === key) {
          index = i
          break
        }
      }
      if (index < components.length - 1) {
        components[index] = components.splice(index + 1, 1, components[index])[0]
      }
      // 如果数据改变了，那么要提示保存
      state.isChangeSaved = false
    }
  }
}

/**
 * 检查表单
 */
export function validate (data) {
  // 返回
  const rs = []
  // 判空
  const isEmpty = (x) => {
    return x === undefined || x === null
  }

  // 发布参数规则
  const rulePublish = {
    categoryId: '分类不能为空',
    newsLangs: '新闻内容不能为空',
    publishTime: '发布时间不能为空',
    recommend: '是否推荐不能为空',
    sort: '排序不能为空',
    status: '发表状态不能为空'
  }

  // 语言内容规则
  const ruleNewsLangs = {
    title: '新闻标题',
    summarize: '新闻简介',
    imageUrl: '新闻图片'
  }

  // 这不是数组
  data.newsLangs.forEach((langs) => {
    for (const key in ruleNewsLangs) {
      if (Object.hasOwnProperty.call(ruleNewsLangs, key)) {
        const msg = ruleNewsLangs[key]
        if (isEmpty(langs[key])) {
          rs.push({ key, msg })
        }
      }
    }
  })

  for (const key in ruleNewsLangs) {
    if (Object.hasOwnProperty.call(rulePublish, key)) {
      const msg = rulePublish[key]
      if (isEmpty(data[key])) {
        rs.push({ key, msg })
      }
    }
  }

  if (rs.length > 0) {
    return rs
  }
  return false
}

/**
 * 语言
 */
export const newsLang = {
  content: '',
  // 显示状态
  enable: 1,
  // 语言数据id
  id: null,
  // 缩略图
  imageUrl: null,
  // 语言 en/ar/es
  language: '',
  params: {},
  remark: null,
  // 简介
  summarize: null,
  // 标题
  title: '',
  updateBy: '',
  updateTime: ''
}
