import _ from 'lodash'
import defaultImg from './empty-image.png'
import defaultImgSmall from './empty-image-small.png'

// 默认数据
const defaultData = {
  title: {
    value: '',
    style: {}
  },
  content: {
    value: '',
    style: {}
  },
  imageLarge: {
    src: '',
    width: undefined,
    height: undefined,
    alt: 'news image'
  },
  imageSmall1: {
    src: '',
    width: undefined,
    height: undefined,
    alt: 'news image'
  },
  imageSmall2: {
    src: '',
    width: undefined,
    height: undefined,
    alt: 'news image'
  }
}
export default {
  // 组件名称
  name: 'MdNewsImageTextInvertedL',
  props: {
    // data 为当前模块的存储数据
    data: {
      type: Object,
      default: () => {
        return defaultData
      }
    }
  },
  data () {
    return {
    }
  },
  async fetch () {
    // 这个请求，在SSG阶段执行
  },
  computed: {
    finalData () {
      return _.defaults(this.data, defaultData)
    }
  },
  watch: {

  },
  mounted () {
  },
  methods: {
    getImageUrl (index) {
      if (this.finalData?.[index]?.src) {
        if (this.finalData?.[index]?.src.indexOf('blob:') === 0) {
          return this.finalData?.[index]?.src
        }
        return process.env.imgBaseUrl + this.finalData?.[index]?.src
      } else {
        if (index === 'imageLarge') {
          return defaultImg
        }
        return defaultImgSmall
      }
    }
    // ajax () {
    //   // 这个请求，在浏览器执行
    // }
  }
}
