import emitter from 'element-ui/src/mixins/emitter'
import Migrating from 'element-ui/src/mixins/migrating'

export default {
  name: 'EditorFormImgUploader',

  mixins: [emitter, Migrating],

  inject: {
    elForm: {
      default: ''
    },
    elFormItem: {
      default: ''
    }
  },

  inheritAttrs: false,

  props: {
    disabled: Boolean,
    readonly: Boolean,
    value: {
      type: Object,
      default () {
        return {
          src: '',
          alt: '',
          width: '',
          height: '',
          videoSrc: '',
          isVideo: false,
          isVideoAutoPlay: true
        }
      }
    }
  },

  data () {
    const propsValue = JSON.parse(JSON.stringify(this.value || {}))
    propsValue.outerWidth = propsValue.outerWidth === undefined ? -1 : propsValue.outerWidth

    return {
      nativeValue: propsValue,
      alt: propsValue.alt,
      videoSrc: propsValue.videoSrc,
      isVideo: propsValue.isVideo ?? false,
      isVideoAutoPlay: propsValue.isVideoAutoPlay ?? false,
      progress: null,
      imgBaseUrl: process.env.imgBaseUrl || ''
    }
  },

  computed: {
    inputDisabled () {
      return this.disabled || (this.elForm || {}).disabled
    }
  },
  watch: {
    // 监听传入属性的改变，以实现v-model
    // value: {
    //   deep: true,
    //   handler () {
    //     this.nativeValue = this.value
    //   }
    // },
    // 监听本地数据的改变，以实现v-model
    nativeValue: {
      deep: true,
      immediate: true,
      handler (val) {
        this.mergeValue()
        this.$emit('input', this.nativeValue)
        this.dispatch('ElFormItem', 'el.form.change', [this.nativeValue])
      }
    }
  },
  mounted () {
  },
  methods: {
    mergeValue () {
      this.nativeValue.alt = this.alt
      this.nativeValue.videoSrc = this.videoSrc
      this.nativeValue.isVideo = this.isVideo ?? false
      this.nativeValue.isVideoAutoPlay = this.isVideoAutoPlay ?? false
      return this.nativeValue
    },
    handleUploadSuccess (uploadFile, uploadFileList, url) {
      this.alt = this.alt ? this.alt : uploadFile.name
      this.handleChange()
    },
    handleChange (event) {
      this.mergeValue()
      this.$emit('change', this.nativeValue)
      this.$emit('input', this.nativeValue)
      this.dispatch('ElFormItem', 'el.form.change', [this.nativeValue])
    },
    handleFocus (event) {
      this.$emit('focus', event)
    },
    handleBlur (event) {
      this.focused = false
      this.$emit('blur', event)
      if (this.validateEvent) {
        this.dispatch('ElFormItem', 'el.form.blur', [this.nativeValue])
      }
    }
  }
}
