/**
 * 设备样式 pc | pad | phone
 */
export const state = () => ({
  // 设备样式：pc/pad/phone
  deviceStyle: 'pc',
  // 舞台缩放
  zoom: 1,
  // 舞台左偏移
  left: 0,
  // 是否显示导航
  helmVisual: true
})
export const mutations = {
  setDeviceStyle (state, dev) {
    if (dev === 'phone') {
      state.zoom = 1
      state.left = 0
    }
    state.deviceStyle = dev
  },
  setZoom (state, zoom) {
    state.zoom = zoom
  },
  incrementZoom (state) {
    state.zoom = Math.min(2, state.zoom + 0.05)
  },
  decrementZoom (state) {
    state.zoom = Math.max(0, state.zoom - 0.05)
  },
  setLeft (state, left) {
    state.left = left
  },
  incrementLeft (state) {
    state.left = Math.min(0.5, state.left + 0.05)
  },
  decrementLeft (state) {
    state.left = Math.max(-0.5, state.left - 0.05)
  },
  toggleHelm (state) {
    state.helmVisual = !state.helmVisual
  }
}
