// import VueComponent from 'vue'
import IconCopyDocument from '~/components/editor/assets/icons/copy.svg?inline'
import IconPagesAdd from '~/components/editor/assets/icons/pages_add.svg?inline'
import IconDesktop from '~/components/editor/assets/icons/desktop.svg?inline'
import IconPhone from '~/components/editor/assets/icons/phone.svg?inline'
import IconWidthFit from '~/components/editor/assets/icons/width-fit.svg?inline'
import IconWidthRest from '~/components/editor/assets/icons/width-1-1.svg?inline'
import IconHelm from '~/components/editor/assets/icons/helm.svg?inline'

export default {
  components: { IconCopyDocument, IconPagesAdd, IconDesktop, IconPhone, IconWidthFit, IconWidthRest, IconHelm },
  data () {
    return {
      contentLangs: process.env.contentLangs || []
    }
  },
  computed: {
    // 当前语言Label
    contentLangLabel () {
      const lang = this.contentLangs.find((item) => {
        return item.code === this.$store.state.news.contentLang
      })
      return lang?.label
    },
    // 监控左侧面板开合，用来计算当前组件宽度
    leftMargin () {
      if (this.$store.state.side.sideLeftShow) {
        return this.$store.state.side.sideLeftWidth
      } else {
        return 0
      }
    },
    // 监控右侧面板开合，用来计算当前组件宽度
    rightMargin () {
      if (this.$store.state.side.sideRightShow) {
        return this.$store.state.side.sideRightWidth
      } else {
        return 0
      }
    },
    // 设备样式，用来设置设备按钮状态
    deviceStyle () {
      return this.$store.state.stage.deviceStyle
    },
    zoom () {
      return this.$store.state.stage.zoom
    },
    stageLeft () {
      return this.$store.state.stage.left
    },
    helmVisual () {
      return this.$store.state.stage.helmVisual
    }
  },
  methods: {
    /**
     * 修改语言
     * @param {*} command
     */
    handleChangeContentLang (val) {
      this.$emit('handleContentTranslate', val)
    },
    /**
     * 拷贝语言内容
     */
    handleCopyModuleOption () {
      this.$store.commit('dialog/setCopyNewsContentVisible', true)
    },
    /**
     * 选择设备
     */
    handleClickDevice (device) {
      this.$store.commit('stage/setDeviceStyle', device)
    },
    /**
     * 滑动缩放
     */
    handleZoom (zoom) {
      this.$store.commit('stage/setZoom', zoom)
    },
    /**
     * 滑动缩放
     */
    handleStageLeft (left) {
      this.$store.commit('stage/setLeft', left)
    },
    /**
     * 原始比例缩放1：1
     */
    handleOriginalZoom () {
      this.handleZoom(1)
      this.handleStageLeft(0)
    },
    /**
     * 自适应缩放比例
     */
    handleAutoZoom () {
      let newZoom = 1
      let newLeft = 0
      if (this.deviceStyle === 'phone') {
        newZoom = 1
        newLeft = 0
      } else {
        const w = window.document.body.clientWidth
        const l = this.leftMargin
        newZoom = (w - this.leftMargin - this.rightMargin - 20) / w
        const w2 = w * newZoom
        newLeft = 0 - ((w / 2 - (w2 + 20) / 2 - l) / w2)
      }
      this.$store.commit('stage/setZoom', parseFloat(newZoom.toFixed(3)))
      this.$store.commit('stage/setLeft', parseFloat(newLeft.toFixed(3)))
    },
    /**
     * 格式化滑块输出
     */
    formatZoomSliderTooltip (value) {
      if (value) {
        return parseFloat((value * 100).toFixed(3)) + '%'
      }
    },
    formatTransSliderTooltip (value) {
      if (!isNaN(Number(value))) {
        const trans = parseFloat((Number(value) * 100).toFixed(3))
        let prefix = 'Center'
        if (trans > 0) {
          prefix = 'Right'
        } else if (trans < 0) {
          prefix = 'Left'
        }

        return `${prefix} ${trans}%`
      }
    },
    /**
     * 显示或隐藏导航栏
     */
    handleToggleHelm () {
      this.$store.commit('stage/toggleHelm')
    }
  }
}
