// import IconEdit from '~/components/editor/assets/icons/edit.svg?inline'
import IconOptions from '~/components/editor/assets/icons/options.svg?inline'
import IconModules from '~/components/editor/assets/icons/modules.svg?inline'
import IconLayers from '~/components/editor/assets/icons/layers.svg?inline'
export default {
  name: 'SideRight',
  components: {
    // IconEdit,
    IconLayers,
    IconOptions,
    IconModules
  },
  props: {
    // 开合样式
    toggleClass: {
      type: String,
      default: () => {
        return ''
      }
    }
  },
  data () {
    return {
      // activeName: 'page-option'
    }
  },
  computed: {
    hasComponents () {
      return this.$store.state.news.data.newsLangs?.[this.$store.state.news.contentLang]?.components?.length > 0
    },
    hasCurrentComponent () {
      return !!this.$store.state.news.currentComponentKey
    },
    toggleClassEnable () {
      return this.$store.state.side.sideRightShow
    },
    activeName: {
      get () {
        return this.$store.state.side.sideRightTabActiveName
      },
      set () {}
    }
  },
  mounted () {
    this.$store.commit('side/setSideRightWidth', this.$el.clientWidth)
  },
  methods: {
    handleClick (tab, event) {
      this.$store.commit('side/setSideRightTabActiveName', tab._props.name)
    },
    // 面板开合按钮事件
    handleToggle (e) {
      this.$store.commit('side/toggleRight')
      this.$store.commit('side/setSideRightWidth', this.$el.clientWidth)
      // this.toggleClassEnable = !this.toggleClassEnable
    }
  }
}
