import { newsEditorPermissions } from '@/utils/auth'
export default {
  name: 'EditorSessionChecker',
  data () {
    return {
      stayOffline: false,
      checkTimer: 0
    }
  },
  mounted () {
    this.checkSession()
  },
  methods: {
    checkSession () {
      if (this.stayOffline) {
        return clearTimeout(this.checkTimer)
      }
      this.$axios.get('/api/getInfo')
        .then(({ data }) => {
          if (data.code === 200 && data.permissions) {
            const existPms = newsEditorPermissions.filter((x) => {
              return data.permissions.includes(x)
            })
            if (existPms.length !== newsEditorPermissions.length) {
              throw new Error('E403')
            }
          } else {
            throw new Error('E401')
          }
        })
        .catch((error) => {
          let msg = this.$t('Your login status has timed out.')
          if (error.message === 'E403') {
            msg = this.$t('You do not have sufficient permissions.')
          }
          this.$confirm(msg + this.$t('Do you want to refresh or stay offline?'), this.$t('Error'), {
            confirmButtonText: this.$t('Refresh'),
            cancelButtonText: this.$t('Stay offline'),
            type: 'warning'
          }).then((data) => {
            // 刷新页面
            window.location.reload()
          }).catch(() => {
            this.stayOffline = true
          }).finally(() => {
            this.checkTimer = setTimeout(() => {
              this.checkSession()
            }, 1000 * 60)
          })
        })
    }
  }
}
