import emitter from 'element-ui/src/mixins/emitter'
import Migrating from 'element-ui/src/mixins/migrating'
import { getToken } from '@/utils/auth'

export default {
  name: 'EditorFormFileUploaderSimple',

  mixins: [emitter, Migrating],

  inject: {
    elForm: {
      default: ''
    },
    elFormItem: {
      default: ''
    }
  },

  inheritAttrs: false,

  props: {
    disabled: Boolean,
    readonly: Boolean,
    value: {
      type: String,
      default () {
        return undefined
      }
    },
    /**
     * 用来从服务器返回中提取文件url
     */
    responseToValue: {
      type: Function,
      default: undefined
    },
    icon: {
      type: String,
      default () {
        return 'el-icon-upload2'
      }
    },
    action: {
      type: String,
      // required: true,
      default () {
        // 默认使用axios的配置前缀
        const url = (this.$axios.defaults.baseURL || '').replace(/\/$/, '') + '/api/common/uploadImage'
        return url
      }
    },
    name: {
      type: String,
      default () {
        return 'file'
      }
    },
    data: {
      type: Object,
      default () {
        return {
          _auth: 'cloud-top news editor for baic'
        }
      }
    },
    limit: {
      type: Number,
      default () {
        return 1
      }
    },
    multiple: {
      type: Boolean,
      default () {
        return false
      }
    },
    headers: {
      type: Object,
      default () {
        return {
          Authorization: 'Bearer ' + getToken()
        }
      }
    },
    // 替换el-upload默认的http request，以处理服务器无论如何都返回http 200带来的onError识别异常
    // eslint-disable-next-line vue/prop-name-casing
    'http-request': {
      type: Function,
      default () {
        /*
         * 上传参数
         * @see node_modules/element-ui/packages/upload/src/ajax.js
         * @type {option}
         */
        const option = arguments[0]

        // 上传表单
        const formData = new FormData()

        // 放置文件
        formData.append(option.filename, option.file, option.file.name)

        // 放置数据
        if (option.data) {
          Object.keys(option.data).forEach((key) => {
            formData.append(key, option.data[key])
          })
        }

        // 放置头部数据
        const headers = option.headers || {}
        headers['Content-Type'] = 'multipart/form-data'

        // 配置头和上传进度
        const config = {
          headers,
          onUploadProgress (e) {
            if (e.total > 0) {
              e.percent = e.loaded / e.total * 100
            }
            option.onProgress(e)
          }
        }

        // 使用axios发送
        return this.$axios.post(option.action, formData, config)
          .then((res) => {
            console.log('上传完成', res, this)
            if (res?.data?.code !== 200 || !res?.data) {
              const msg = res?.data?.msg
              const err = new Error(msg)
              err.status = res.code
              err.method = 'post'
              err.url = option.action
              return option.onError(err)
            }

            option.onSuccess(res.data)
          })
          .catch((e) => {
            option.onError(e)
          })
      }
    }
  },

  data () {
    return {
      nativeValue: this.value,
      isProgressing: false,
      isShowUploadError: false
    }
  },

  computed: {
    inputDisabled () {
      return this.disabled || (this.elForm || {}).disabled
    }
  },
  watch: {
  },
  mounted () {
  },
  methods: {
    onProgress (event, file, fileList) {
      this.isProgressing = true
      if (typeof this.$attrs?.['on-progress'] === 'function') {
        this.$attrs?.['on-progress'](event, file, fileList)
      }
    },
    onSuccess (response, file, fileList) {
      this.isProgressing = false
      if (this.responseToValue) {
        this.nativeValue = this.responseToValue(response)
      } else {
        this.nativeValue = response.url
      }
      // 清空，为了可以重复上传文件
      this.$refs.upload.clearFiles()
      if (typeof this.$attrs?.['on-success'] === 'function') {
        this.$attrs?.['on-success'](response, file, fileList)
      }

      // 触发change事件
      this.handleInput()
      this.handleChange(file, fileList)
    },
    onError (err, file, fileList) {
      this.isProgressing = false
      this.isShowUploadError = true
      this.nativeValue = undefined
      // 清空，为了可以重复上传文件
      this.$refs.upload.clearFiles()
      if (typeof this.$attrs?.['on-error'] === 'function') {
        this.$attrs?.['on-error'](err, file, fileList)
      } else {
        this.$message({
          message: this.$t('Upload failed') + err.message,
          type: 'error'
        })
      }

      // 触发change事件
      this.handleInput()
      this.handleChange(file, fileList)
    },
    handleInput (event) {
      this.$emit('input', this.nativeValue)
    },
    handleBeforeUpload (file) {
    },
    handleChange (file, fileList) {
      this.$emit('change', file, fileList, this.nativeValue)
      this.dispatch('ElFormItem', 'el.form.change', [this.nativeValue])
    },
    handleFocus (event) {
      // 清除可能的错误
      this.isShowUploadError = false
      this.$emit('focus', event)
    },
    handleBlur (event) {
      this.focused = false
      this.$emit('blur', event)
      if (this.validateEvent) {
        this.dispatch('ElFormItem', 'el.form.blur', [this.nativeValue])
      }
    }
  }
}
