import IconLayers from '~/components/editor/assets/icons/layers.svg?inline'
export default {
  name: 'EditorPanelLayers',
  components: { IconLayers },
  data () {
    return {
      list: []
    }
  },
  computed: {
    components () {
      const components = this.$store.state.news.data.newsLangs?.[this.$store.state.news.contentLang]?.components
      return JSON.parse(JSON.stringify(components || []))
    },
    currentComponentKey () {
      return this.$store.state.news.currentComponentKey
    }
  },
  methods: {
    /**
     * 选中某个组件时
     * @param page
     */
    handleClick (component) {
      this.$store.commit('news/setCurrentComponentKey', component.key)
      this.$store.commit('side/setSideRightTabActiveName', 'module-option')
    },
    handleCommand (command) {
      switch (command) {
        case 'up':
          this.handleMoveUp()
          break
        case 'down':
          this.handleMoveDown()
          break
        case 'delete':
          this.handleRemove()
          break
        default:
          break
      }
    },
    handleMoveUp () {
      this.$store.commit('news/moveUpComponents', this.currentComponentKey)
    },
    handleMoveDown () {
      this.$store.commit('news/moveDownComponents', this.currentComponentKey)
    },
    handleRemove () {
      this.$confirm(this.$t('Are you sure you want to delete it'), this.$t('Warning'), {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancel',
        type: 'warning'
      }).then(() => {
        this.$store.commit('news/removeComponents', this.currentComponentKey)
      })
    }
  }
}
