/**
 * 用于iframe之间的vuex通信
 * @see https://github.com/qq1147050160/vuex-iframe-share
 */
import vuexIframeShare from '~/plugins/vuex-iframe-share'
/**
 * strict = false
 * to fix Error: [vuex] Do not mutate vuex store state outside mutation handlers
 * @see 'https://github.com/nuxt/nuxt.js/issues/1917'
 */
export const strict = false
export const plugins = [vuexIframeShare.child(), vuexIframeShare.parent()]

export const state = () => ({
  // 所有的页面 {key, name, path}
  pages: []
})

export const mutations = {
}

export const actions = {
  nuxtServerInit ({ commit }) {
    commit('returnComs', process.env.COMS)
  }
}
