/**
 * 模块的编辑器规则
 */
import _ from 'lodash'
import editorSchema from '@/static/temp/editor.json'
import packageJson from '@/static/temp/package.json'
export default {
  name: 'EditorPanelModuleOption',
  components: {
    // 用来将VNode挂载到template
    Vnodes: {
      functional: true,
      render: (h, ctx) => ctx.props.vnodes
    }
  },
  props: {
    // 是否是设置全局的
    isWebSite: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    // 页面id
    pageId: {
      type: String,
      default: () => {
        return ''
      }
    }
  },
  data () {
    return {
      formDom: null,
      activeNames: ['0'],
      path: '',
      metaInfo: {
        title: '',
        titleTemplatePrefix: '%s - Baz',
        titleTemplateSubfix: '%s - Baz',
        seo: {
          keywords: '',
          description: '',
          combinationMode: 'pre'
        },
        meta: [{
          name: 'viewport',
          content: 'sbd'
        }],
        link: [
          { rel: 'stylesheet', href: '/css/index.css' },
          { rel: 'favicon', href: 'favicon.ico' }
        ],
        style: [
          { cssText: '.foo { color: red }', type: 'text/css' }
        ],
        script: [
          { src: 'https://cdn.jsdelivr.net/npm/vue/dist/vue.js', async: false, defer: false }
        ],
        innerScript: [
          { innerHTML: '', type: 'text/javascript' }
        ]
      }
    }
  },
  computed: {
    /**
     * 当前选中的模块
     */
    currentComponent () {
      const key = this.$store.state.news.currentComponentKey
      const data = this.$store.state.news.data.newsLangs?.[this.$store.state.news.contentLang]
      if (key && data && data.components && _.isArray(data.components)) {
        for (let i = 0; i < data.components.length; i++) {
          if (data.components[i].key === key) {
            return data.components[i]
          }
        }
      }
      return undefined
    },
    /**
     * 当前选中的模块名字
     */
    currentComponentName () {
      if (!_.isEmpty(this.currentComponent)) {
        return this.currentComponent.name
      }
      return null
    },
    /**
     * 当前选中的模块分布式ID
     */
    currentComponentKey () {
      return this.$store.state.news.currentComponentKey
    },
    /**
     * 当前选中的模块规则
     */
    currentComponentSchema () {
      if (editorSchema && this.currentComponentName) {
        return editorSchema[this.currentComponentName]
      }
      return null
    },
    /**
     * 当前选中的 当前内容语言的 模块规则数据
     */
    currentComponentData () {
      return this.currentComponent?.data
    },
    /**
     * 当前选中的模块的全部package数据
     */
    currentComponentPackageJson () {
      if (_.isArray(packageJson) && this.currentComponentName) {
        return _.find(packageJson, item => item.name === this.currentComponentName)
      }
      return null
    }
  },
  watch: {
    /**
     * 当选中的组件key改变时
     */
    currentComponentKey: {
      deep: true,
      immediate: true,
      handler (a, b) {
        if (a !== b) {
          this.fcBuildFormDom()
        }
      }
    }
  },
  methods: {
    // 拷贝模块属性
    copyModuleOption () {
      this.$store.commit('dialog/setCopyModuleOptionVisible', true)
    },
    handleSubmit (data) {
      this.$store.commit('news/setComponentData', { key: this.currentComponentKey, data })
    },
    handleMoveUp () {
      this.$store.commit('news/moveUpComponents', this.currentComponentKey)
    },
    handleMoveDown () {
      this.$store.commit('news/moveDownComponents', this.currentComponentKey)
    },
    handleRemove () {
      this.$confirm(this.$t('Are you sure you want to delete it'), this.$t('Warning'), {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancel',
        type: 'warning'
      }).then(() => {
        this.$store.commit('news/removeComponents', this.currentComponentKey)
      })
    },
    /**
     * 创建表单dom
     */
    fcBuildFormDom () {
      const camelizeRE = /-(\w)/g
      // 静态编辑器名称
      const tagStatic = this.currentComponentName + '-editor'
      const tagStaticUF = _.upperFirst(tagStatic.replace(camelizeRE, (_, c) => c ? c.toUpperCase() : ''))
      // 动态编辑器名称
      const tagDynamic = 'editor-form-dynamic-' + this.currentComponentName
      const tagDynamicUF = _.upperFirst(tagDynamic.replace(camelizeRE, (_, c) => c ? c.toUpperCase() : ''))

      // 优先选择静态编辑器
      let tag = ''
      if (this.$options.components[tagStaticUF]) {
        tag = tagStatic
      } else if (this.$options.components[tagDynamicUF]) {
        tag = tagDynamic
      } else {
        console.error('无法发现表单编辑器', tagStatic, ' 或 ', tagDynamic)
        this.formDom = []
        return
      }
      console.log('module option set props', this.currentComponentData)
      this.formDom = this.$createElement(tag, {
        key: Math.random() + (new Date()).getTime(),
        props: {
          vIf: this.currentComponentPackageJson,
          schema: this.currentComponentSchema,
          formData: this.currentComponentData
        },
        on: {
          onSubmit: this.handleSubmit
        }
      })
      this.formDom.key = Math.random() + (new Date()).getTime()
    },
    // 当前语言方向
    contentLangDirClass () {
      const lang = process.env.contentLangs.find((item) => {
        return item.code === this.$store.state.news.contentLang
      })
      return `ed-dir-${lang?.dir}`
    }
  }
}
