/**
 * 添加新页面
 */
export const state = () => ({
  // 拷贝模块参数数据
  copyNewsContentVisible: false,
  copyModuleOptionVisible: false
})
export const mutations = {
  setCopyNewsContentVisible (state, visible) {
    state.copyNewsContentVisible = visible
  },
  setCopyModuleOptionVisible (state, visible) {
    state.copyModuleOptionVisible = visible
  }
}
