/**
 * 这里有几个问题
 */
// import IconRedo from '~/components/editor/assets/icons/redo.svg?inline'
// import IconUndo from '~/components/editor/assets/icons/undo.svg?inline'
import IconPreview from '~/components/editor/assets/icons/preview.svg?inline'
import IconSave from '~/components/editor/assets/icons/save.svg?inline'
// import IconPublish from '~/components/editor/assets/icons/publish.svg?inline'
// import IconSetting from '~/components/editor/assets/icons/setting.svg?inline'
import IconTranslate from '~/components/editor/assets/icons/translate.svg?inline'
import IconImport from '~/components/editor/assets/icons/import.svg?inline'
import IconExport from '~/components/editor/assets/icons/export.svg?inline'

export default {
  components: { IconPreview, IconSave, IconTranslate, IconImport, IconExport },
  data () {
    return {
      saveLock: false,
      contentLangs: process.env.contentLangs || []
    }
  },
  computed: {
    isChangeSaved () {
      return this.$store.state.news.isChangeSaved
    },
    // 当前语言
    currentContentLang () {
      return this.$store.state.news.contentLang
    },
    previewSrc () {
      // const isDefault = this.$i18n.defaultLocale === this.currentContentLang
      // return isDefault ? './preview' : './' + this.currentContentLang + '/preview'
      return './preview'
    },
    locales () {
      return this.$i18n.locales || [{ code: 'en', iso: 'en-US', label: 'English', file: 'en.js', dir: 'ltr' }]
    },
    jsonNewsData () {
      const originalNews = this.$store.state.news
      const { isChangeSaved, newId, ...rest } = originalNews
      const filteredKeys = ['id', 'updateBy', 'updateTime', 'createBy', 'createTime', 'pageType', 'publishTime']
      const result = this.filterNestedObjProps(rest, filteredKeys)
      console.log(result, '导出数据')
      // 确保json字符串中的空格被正确编码
      return encodeURIComponent(JSON.stringify(result))
    }
  },
  watch: {
  },
  mounted () {
    const that = this
    // 系统可能不会保存您所做的更改。
    window.addEventListener('beforeunload', (e) => {
      // console.log('that.saveLock:', that.isChangeSaved)
      if (!that.isChangeSaved) {
        e.preventDefault()
        e.returnValue = ''
        return ''
      }
    })
  },
  methods: {
    /**
     * 选择语言
     * @param command
     */
    handleTranslate (command) {
      this.$i18n.setLocaleCookie(command)
      this.$i18n.setLocale(command)
    },
    /**
     * 保存
     */
    async handleSave () {
      // 检查表单
      if (this.$nuxt.__checkFormPublish && !(await this.$nuxt.__checkFormPublish.validate().catch(x => false))) {
        this.$alert('【新闻发布配置】表单有未完成的项目', '表单检查失败', { type: 'error' }).then(() => {
          this.$store.commit('side/openLeft')
          this.$store.commit('side/setSideLeftTabActiveName', 'first')
        })
        return
      }
      if (this.$nuxt.__checkFormLangs) {
        const formRefs = this.$nuxt.__checkFormLangs
        for (const langCode in formRefs) {
          const fromRef = formRefs[langCode]
          if (!(await fromRef.validate().catch(x => false))) {
            const langLabel = (this.contentLangs.find(x => x.code === langCode))?.label || langCode
            this.$alert(langLabel + '的【新闻标题和图片】表单有未完成的项目', '表单检查失败', { type: 'error' }).then(() => {
              this.$store.commit('side/openLeft')
              this.$store.commit('side/setSideLeftTabActiveName', 'second')
              this.$store.commit('side/setNewsTitleActiveCollapse', langCode)
            })
            return
          }
        }
      }

      if (this.saveLock !== true) {
        this.saveLock = true
        setTimeout(() => { this.saveLock = false }, 10 * 1000)
      }
      /* if (this.isChangeSaved) {
        this.saveLock = false
        return
      } */

      // 发送数据
      const data = JSON.parse(JSON.stringify(this.$store.state.news.data || {}))
      // 处理 newsLangs
      const newsLangsArr = []
      for (const language in data.newsLangs) {
        const newsLangData = data.newsLangs[language]
        newsLangData.language = language
        newsLangData.content = JSON.stringify(newsLangData.components) || ''
        delete newsLangData.components
        newsLangsArr.push(newsLangData)
      }
      data.newsLangs = newsLangsArr

      // 编辑还是保存
      let savePromise = {}
      let isAdd = true
      if (this.$store.state.news?.data?.id) {
        savePromise = this.$axios.put('/api/system/news', data)
        isAdd = false
      } else {
        savePromise = this.$axios.post('/api/system/news', data)
      }
      savePromise
        .then((res) => {
          if (res?.data?.code !== 200 || (isAdd && !(res?.data?.data?.id))) {
            throw new Error(res?.data?.msg || '')
          }
          if (res?.data?.data?.id) {
            this.$store.commit('news/setNewsId', res?.data?.data?.id)
          }
          this.$store.commit('news/setIsChangeSaved', true)
          this.$message({
            message: this.$t('Save OK'),
            type: 'success'
          })
        })
        .catch((e) => {
          this.$message({
            message: this.$t('Save failed, Please try again later') + e.message,
            type: 'error'
          })
        })
        .finally(() => {
          this.saveLock = false
        })
    },
    /**
     * 预览
     */
    handlePreview () {
      window.localStorage.setItem('___editor_preview_data', JSON.stringify(this.$store.state.news))
    },
    /**
     * 选择界面语言
     */
    handleUiTranslate () {
      this.$i18n.setLocaleCookie(this.$i18n.locale)
      this.$i18n.setLocale(this.$i18n.locale)
    },
    /**
     * 选择内容语言
     */
    handleContentTranslate (val) {
      this.$emit('handleContentTranslate', val)
      // 判断是否保存
      this.$refs.dropdown.hide()
    },
    /**
     * 设置状态为需要保存
     */
    fcSetNeedSaveSate () {
      this.$store.commit('news/setIsChangeSaved', false)
    },
    // 导入json文件前
    beforeUpload (file) {
      const reader = new FileReader()
      reader.onload = (e) => {
        const jsonData = JSON.parse(e.target.result)
        // console.log(jsonData, 'json数据')
        // 数据校验
        const requireField = ['contentLang', 'currentComponentKey', 'data']
        const hasAllProps = requireField.every((item) => {
          console.log(item in jsonData, '----' + item)
          return item in jsonData
        })
        const hasNewsProp = jsonData.data && 'newsLangs' in jsonData.data
        console.log(hasNewsProp, '是否有newsLangs')
        if (hasAllProps && hasNewsProp) {
          console.log(jsonData, '导入的数据')
          // 添加发布日期为当前日期
          jsonData.data.publishTime = new Date()
          console.log(jsonData, '添加发布时间')
          // console.log('包含必须的属性')
          this.$confirm('是否覆盖当前新闻的数据', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            // console.log(jsonData.newId, 'news id')
            // 写入数据
            this.$store.commit('news/setContentLang', jsonData.contentLang)
            this.$store.commit('news/setNewsData', jsonData.data)
            this.$store.commit('news/setCurrentComponentKey', jsonData.currentComponentKey)
            this.$store.commit('news/setIsChangeSaved', false)
            this.$message({
              type: 'success',
              message: '操作成功!'
            })
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消'
            })
          })
        } else {
          // console.log('文件错误')
          this.$message.error('文件数据格式错误')
        }
      }
      reader.readAsText(file)
      return false // 阻止上传
    },
    // 过滤掉嵌套对象的特定属性
    filterNestedObjProps (obj, filteredProps) {
      if (typeof obj !== 'object' || obj === null) {
        return obj
      }
      if (Array.isArray(obj)) {
        return obj.map(item => this.filterNestedObjProps(item, filteredProps))
      }
      const result = {}
      for (const key in obj) {
        // 去掉对象自身的某些属性
        if (Object.prototype.hasOwnProperty.call(obj, key) && !filteredProps.includes(key)) {
          // 处理嵌套的对象
          result[key] = this.filterNestedObjProps(obj[key], filteredProps)
        }
      }
      return result
    }
  }
}
