/**
 * 左右slid的开合
 * @return {{sideRightShow: boolean, sideLeftShow: boolean}}
 */
export const state = () => ({
  sideLeftShow: false,
  sideRightShow: true,
  sideLeftWidth: 0,
  sideRightWidth: 0,
  sideRightTabActiveName: 'modules',
  sideLeftTabActiveName: 'second',
  newsTitleActiveCollapse: ''
})
export const mutations = {
  openLeft (state) {
    state.sideLeftShow = true
  },
  openRight (state) {
    state.sideRightShow = true
  },
  toggleLeft (state) {
    state.sideLeftShow = !state.sideLeftShow
  },
  toggleRight (state) {
    state.sideRightShow = !state.sideRightShow
  },
  setSideLeftWidth (state, width) {
    state.sideLeftWidth = width
  },
  setSideRightWidth (state, width) {
    state.sideRightWidth = width
  },
  setSideRightTabActiveName (state, activeName) {
    state.sideRightTabActiveName = activeName
  },
  setSideLeftTabActiveName (state, activeName) {
    state.sideLeftTabActiveName = activeName
  },
  setNewsTitleActiveCollapse (state, activeName) {
    state.newsTitleActiveCollapse = activeName
  }
}
