export default {
  name: 'MainStage',
  data () {
    return {
      // currentLang: this.$i18n.defaultLocale
    }
  },
  computed: {
    // 设备样式，用来设置设备按钮状态
    deviceStyle () {
      return this.$store.state.stage.deviceStyle
    },
    zoom () {
      return this.$store.state.stage.zoom
    },
    contentLang () {
      return this.$store.state.news.contentLang
    }
  },

  watch: {
    contentLang (newVal, oldVal) {
      // 重新加载iframe
      document.getElementById('mainStage').contentWindow.location.reload(true)
    }
  },
  mounted () {
    document.getElementById('mainStage').contentWindow.location.reload(true)
    // 监听ctrl + wheel
    this.$el.addEventListener('wheel', this.handleCtrlWheel, false)
  },
  methods: {
    handleClickDevice (device) {
      this.$store.commit('stage/setDevicesStyle', device)
    },
    handleCtrlWheel (e) {
      if (e.ctrlKey || e.shiftKey) {
        e.preventDefault()
        e.stopPropagation()

        if (e.deltaY > 0) {
          if (e.ctrlKey) {
            this.$store.commit('stage/incrementZoom')
          }
          if (e.shiftKey) {
            this.$store.commit('stage/incrementLeft')
          }
        } else if (e.deltaY < 0) {
          if (e.ctrlKey) {
            this.$store.commit('stage/decrementZoom')
          }
          if (e.shiftKey) {
            this.$store.commit('stage/decrementLeft')
          }
        }
      }
      if (e.ctrlKey) {
        e.returnValue = false
      }
    }
  }
}
