// import uuid from '@/utils/uuid'
import CopyNewsContentSvg from '~/components/editor/assets/svg/copy-news-content.svg?inline'

export default {
  components: { CopyNewsContentSvg },
  props: {
    dialogProps: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      form: {
        sourceLang: undefined
      },
      rules: {
        sourceLang: [
          { required: true, message: 'Please select the source language', trigger: 'change' }
        ]
      }
    }
  },
  computed: {
    visible: {
      get () {
        return this.$store.state.dialog.copyNewsContentVisible
      },
      set (visible) {
        this.$store.commit('dialog/setCopyNewsContentVisible', visible)
      }
    },
    // 当前语言代码
    currentContentLang () {
      return this.$store.state.news.contentLang
    },
    // 当前语言Label
    contentLangLabel () {
      const lang = process.env.contentLangs.find((item) => {
        return item.code === this.$store.state.news.contentLang
      })
      return lang?.label
    }
  },
  watch: {

  },
  mounted () {
  },
  methods: {
    setVisible (visible) {
      this.$store.commit('dialog/setCopyNewsContentVisible', visible)
    },
    handleCopyLanguageContent () {
      this.$refs.form.validate((valid) => {
        if (valid) {
          // 获得源语言code
          const fromLocal = this.form.sourceLang
          this.$store.commit('news/copyComponentsForLang', fromLocal)
          this.setVisible(false)
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 当前语言数据是否为空
    currentComponentDataIsEmpty () {
      const components = this.$store.state.news?.data?.newsLangs?.[this.currentContentLang]?.components
      // console.log('当前语言数据是否为空', components, components.length, !(components && (components.length > 0)))
      return !(components && (components.length > 0))
    },
    // 设置来源语言
    getSourceLangOptions () {
      return process.env.contentLangs.map((x) => {
        const components = this.$store.state.news?.data?.newsLangs?.[x.code]?.components
        // 如果没有内容，或者是当前语言，禁用
        x.disabled = !(components && (components.length >= 0))
        // x.disabled = x.disabled || (x.code === this.currentContentLang)
        return x
      }).filter(x => x.code !== this.currentContentLang)
    }

  }
}
