// import IconPages from '~/components/editor/assets/icons/pages.svg?inline'
// import IconLayers from '~/components/editor/assets/icons/layers.svg?inline'
// import IconTitle from '~/components/editor/assets/icons/title.svg?inline'
// import IconSetting from '~/components/editor/assets/icons/setting.svg?inline'
export default {
  name: 'SideLeft',
  components: {
    // IconPages,
    // IconLayers,
    // IconSetting
    // IconTitle
  },
  props: {
    // 开合样式
    toggleClass: {
      type: String,
      default: () => {
        return ''
      }
    }
  },
  data () {
    return {
      // activeName: 'first'
    }
  },
  computed: {
    toggleClassEnable () {
      return this.$store.state.side.sideLeftShow
    },
    activeName: {
      get () {
        return this.$store.state.side.sideLeftTabActiveName
      },
      set () {}
    }
  },
  mounted () {
    this.$store.commit('side/setSideLeftWidth', this.$el.clientWidth)
  },
  methods: {
    handleClick (tab, event) {
      this.$store.commit('side/setSideLeftTabActiveName', tab._props.name)
    },
    // 面板开合按钮事件
    handleToggle (e) {
      this.$store.commit('side/toggleLeft')
      this.$store.commit('side/setSideLeftWidth', this.$el.clientWidth)
      // this.toggleClassEnable = !this.toggleClassEnable
    }
  }
}
