import _ from 'lodash'
import { setOne } from 'vue-set-path'
const defaultValue = {
  "textPicSpace": 1
}
export default {
  name: 'editor-form-dynamic-md-news-image-large',
  data () {
    return {
      fData: JSON.parse(JSON.stringify(this.formDataWithDefault()))
    }
  },
  props: {
    // 当前模块的Schema
    schema: {
      type: Object,
      default () {
        return {}
      }
    },
    // 当前模块的数据
    formData: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  computed: {
  },
  mounted () {
  },
  watch: {
    formData: {
      deep: true,
      immediate: true,
      handler () {
        this.fData = JSON.parse(JSON.stringify(this.formDataWithDefault()))
      }
    }
  },
  methods: {
    // 递归的合并属性用默认值填充
    formDataWithDefault () {
      return _.merge((defaultValue || {}), this.formData)
    },
    /**
     * 添加一个分组
     */
    handleAddCard (path) {
      const newList = _.get(this.fData, path, [])
      newList.push({})
      setOne(this.fData, path, newList)
    },
    /**
     * 删除一个分组
     */
    handleRemoveCard (path, i) {
      const newList = _.get(this.fData, path, [])
      newList.splice(i, 1)
      setOne(this.fData, path, newList)
    },
    /**
     * 提交表单
     */
    handleSubmit () {
      this.$emit('onSubmit', JSON.parse(JSON.stringify(this.fData)))
    }
  }
}
