import _ from 'lodash'
export default {
  name: 'EditorFormSytleTextInput',
  props: {
    defaultValue: {
      type: String,
      default: undefined
    },
    value: {
      type: Object,
      default () {
        return {
          value: this.defaultValue || 'place input text',
          style: {}
        }
      }
    },
    placeholder: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      // 是否展开样式控件
      isShowCollapse: 0,
      inputValues: {
        value: '',
        style: {
        // 字号
          fontSize: undefined,
          // 对其方式
          textAlign: undefined,
          // 加粗
          fontWeight: undefined,
          // 斜体
          fontStyle: undefined,
          // 首行缩进
          textIndent: undefined,
          // 颜色
          color: undefined,
          // 行高
          lineHeight: undefined,
          // 下划线-删除线
          textDecoration: undefined,
          // 大小写
          textTransform: undefined,
          // 空白字符换行处理
          whiteSpace: undefined,
          // 单词内换行
          overflowWrap: undefined
        }
      }
    }
  },
  computed: {
    /* style () {
      return this.inputValues.style
    } */
  },
  watch: {
    inputValues: {
      deep: true,
      handler (newVal) {
        this.$emit('input', newVal)
        this.$emit('change', newVal)
      }
    }
  },
  mounted () {
    // 如果传入的值是字符串，那么作为默认值处理并发出警告
    if (typeof this.value === 'string') {
      this.inputValues = { value: this.value, style: {} }
      console.warn('EditorFormSytleTextInput `value` prop suggest using objects like `{value:"", style:{}}`')
    } else {
      this.inputValues = _.merge({ value: undefined, style: {} }, this.value)
    }
  },
  methods: {
    /**
     * 清空样式
     */
    clearStyleHandle () {
      this.inputValues.style = {}
    }
  }
}
