import emitter from 'element-ui/src/mixins/emitter'
import Migrating from 'element-ui/src/mixins/migrating'
export default {
  name: 'EditorFormSimpleSelect',
  mixins: [emitter, Migrating],
  inject: {
    elForm: {
      default: ''
    },
    elFormItem: {
      default: ''
    }
  },
  inheritAttrs: false,
  props: {
    disabled: Boolean,
    readonly: Boolean,
    // eslint-disable-next-line vue/require-default-prop
    size: String,
    multiple: Boolean,
    clearable: Boolean,
    // eslint-disable-next-line vue/require-default-prop
    placeholder: String,
    // eslint-disable-next-line vue/require-prop-types
    value: {
      required: true
    },
    options: {
      type: Array,
      default () {
        return []
      }
    }
  }
}
