export default {
  name: 'BannerSelect',
  props: {
    // 这里可能是 java long，会超出js number 长度，所以用string
    value: String
  },

  data () {
    return {
      options: [],
      val: null,
      loading: true
    }
  },

  mounted () {
    this.$axios.get('apis/banner/group').then((res) => {
      const list = res.data.content
      list.forEach((item) => {
        this.options.push({
          label: item.name,
          value: String(item.id)
        })
      })
      this.loading = false
      // 如果找不到value则赋值为空，避免值为undefined的情况
      this.val = this.value ? String(this.value) : ''
    })
  },

  methods: {
    changeValue (val) {
      this.$emit('input', val)
    }
  }
}
