import _ from 'lodash'
import CopyModuleOptionsSvg from '~/components/editor/assets/svg/copy-module-options.svg?inline'
// import uuid from '@/utils/uuid'

export default {
  components: {
    // 用来将VNode挂载到template
    Vnodes: {
      functional: true,
      render: (h, ctx) => ctx.props.vnodes
    },
    CopyModuleOptionsSvg
  },
  props: {
    dialogProps: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      form: {
        sourceLang: undefined,
        targetLangs: [],
        options: []
      },
      rules: {
        targetLangs: [
          { required: true, message: 'Please select the language', trigger: 'change' }
        ],
        sourceLang: [
          { required: true, message: 'Please select the language', trigger: 'change' }
        ],
        options: [
          { required: true, message: 'Please select the language', trigger: 'change' }
        ]
      },
      optionsSelectorDom: []
    }
  },
  computed: {
    visible: {
      get () {
        return this.$store.state.dialog.copyModuleOptionVisible
      },
      set (visible) {
        this.$store.commit('dialog/setCopyModuleOptionVisible', visible)
      }
    },
    // 当前语言代码
    currentContentLang () {
      return this.$store.state.news.contentLang
    },
    // 当前语言Label
    contentLangLabel () {
      const lang = process.env.contentLangs.find((item) => {
        return item.code === this.$store.state.news.contentLang
      })
      return lang?.label
    },
    /**
     * 当前选中的模块名字
     */
    currentComponentName () {
      return this.getCurrentComponent()?.name
    }
  },
  watch: {
  },
  mounted () {
    this.restForm()
  },
  methods: {
    // 重置表单
    restForm () {
      // 默认选中目标
      this.form.sourceLang = this.currentContentLang
      this.form.targetLangs = this.getSameModulesFromOtherLanguage().filter(x => !x.disabled).map(x => x.code)
      this.form.options = []
    },
    // 窗口回调
    openedHandle () {
      this.restForm()
      this.buildOptionsSelector()
    },
    setVisible (visible) {
      this.$store.commit('dialog/setCopyModuleOptionVisible', visible)
    },
    handleCopyOptions () {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.$confirm(
            this.$t('This operation will overwrite the content of the target language. Do you want to continue?'),
            this.$t('Warnning'), {
              confirmButtonText: this.$t('OK'),
              cancelButtonText: this.$t('Cancel'),
              type: 'warning'
            }).then(() => {
            // 获得源语言code,
            const sourceKey = this.$store.state.news.currentComponentKey
            this.$store.commit('news/copyComponentsDataToLang', { ...this.form, sourceKey })
            this.$alert(this.$t('Copy Success, please switch languages to view the results.')).then(() => {
              this.setVisible(false)
            })
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 当前语言数据是否为空
    currentComponentDataIsEmpty () {
      const components = this.$store.state.news?.data?.newsLangs?.[this.currentContentLang]?.components
      // console.log('当前语言数据是否为空', components, components.length, !(components && (components.length > 0)))
      return !(components && (components.length > 0))
    },
    // 设置来源语言
    getSourceLangOptions () {
      return process.env.contentLangs.map((x) => {
        const components = this.$store.state.news?.data?.newsLangs?.[x.code]?.components
        // 如果没有内容，或者是当前语言，禁用
        x.disabled = !(components && (components.length >= 0))
        x.disabled = x.disabled || (x.code === this.currentContentLang)
        return x
      })
    },
    /**
     * 根据语言code和模块的key获取模块数据
     */
    getComponentByLangAndKey (langCode, moduleKey) {
      const key = moduleKey
      const data = this.$store.state.news.data.newsLangs?.[langCode]
      if (key && data && data.components && _.isArray(data.components)) {
        for (let i = 0; i < data.components.length; i++) {
          if (data.components[i].key === key) {
            return data.components[i]
          }
        }
      }
      return undefined
    },
    /**
     * 当前选中的模块
     */
    getCurrentComponent () {
      const key = this.$store.state.news.currentComponentKey
      return this.getComponentByLangAndKey(this.$store.state.news.contentLang, key)
    },
    /**
     * 当前选中的 当前内容语言的 模块规则数据
     */
    getCurrentComponentData () {
      return this.getCurrentComponent()?.data
    },
    /**
     * 看下其他语言有没有相同的模块
     */
    getSameModulesFromOtherLanguage () {
      const currKey = this.$store.state.news.currentComponentKey
      return process.env.contentLangs.map((x) => {
        const existsSameKey = this.getComponentByLangAndKey(x.code, currKey)
        x.disabled = !existsSameKey
        return x
      }).filter(x => x.code !== this.currentContentLang)
    },
    /**
     * 动态构造组件需要复制的可选项目
     */
    buildOptionsSelector () {
      const camelizeRE = /-(\w)/g
      // 静态编辑器名称
      const tagStatic = this.currentComponentName + '-copy'
      const tagStaticUF = _.upperFirst(tagStatic.replace(camelizeRE, (_, c) => c ? c.toUpperCase() : ''))
      // 动态编辑器名称
      const tagDynamic = 'editor-form-dynamic-copy-' + this.currentComponentName
      const tagDynamicUF = _.upperFirst(tagDynamic.replace(camelizeRE, (_, c) => c ? c.toUpperCase() : ''))

      // 优先选择静态编辑器
      let tag = ''
      if (this.$options.components[tagStaticUF]) {
        tag = tagStatic
      } else if (this.$options.components[tagDynamicUF]) {
        tag = tagDynamic
      } else {
        console.error('无法发现表单复制器', tagStatic, ' 或 ', tagDynamic)
        this.formDom = []
        return
      }

      this.optionsSelectorDom = this.$createElement(tag, {
        key: Math.random() + (new Date()).getTime(),
        on: {
          change: (x) => {
            this.form.options = x
          }
        }
      })
    }
  }
}
