export default {
  name: 'EditorPanelNewsTitle',
  props: {
    // 页面id
    pageId: {
      type: String,
      default: () => {
        return ''
      }
    }
  },
  data () {
    return {
      saveGeneralButtonLoading: false,
      contentLangs: process.env.contentLangs || []
    }
  },
  computed: {
    newsLangsData () {
      return this.$store.state.news.data.newsLangs
    },
    activeLang: {
      get () {
        return this.$store.state.side.newsTitleActiveCollapse
      },
      set () {}
    }
  },
  watch: {
  },
  mounted () {
    // 将表单挂载到全局以供在保存时检查表单使用
    process.env.contentLangs.forEach((lc) => {
      if (this.$refs[`form-${lc.code}`][0]) {
        this.$nuxt.__checkFormLangs = this.$nuxt.__checkFormLangs || {}
        this.$nuxt.__checkFormLangs[lc.code] = this.$refs[`form-${lc.code}`][0]
      }
    })
  },
  methods: {
    /**
     * 当表单改变时，修改vuex中的newslangs的标题和缩略图等数据
     */
    changeNewslangsTitleData () {
      this.$store.commit('news/setNewsLangsExcludeContent', this.newsLangsData)
    },
    handleCollapseChange (val) {
      this.$store.commit('side/setNewsTitleActiveCollapse', val)
    }
  }
}
