import _ from 'lodash'

// 默认数据
const defaultData = {
  title: {
    value: 'place input the title'
  },
  scpaceTop: 1,
  scpaceBottom: 1
}
export default {
  // 组件名称
  name: 'MdNewsTitle',
  props: {
    // data 为当前模块的存储数据
    data: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
    }
  },
  async fetch () {
    // 这个请求，在SSG阶段执行
  },
  computed: {
    finalData () {
      return _.defaults(this.data, defaultData)
    },
    newsStyle () {
      return {
        marginTop: (this.finalData.scpaceTop !== undefined) ? this.finalData.scpaceTop + 'em' : '',
        marginBottom: (this.finalData.scpaceBottom !== undefined) ? this.finalData.scpaceBottom + 'em' : ''
      }
    }
  },
  watch: {

  },
  mounted () {
  },
  methods: {
  }
}
