export default {
  name: 'EditorPanelNewsTitle',
  props: {
    // 页面id
    pageId: {
      type: String,
      default: () => {
        return ''
      }
    }
  },
  data () {
    return {
      categoryOptions: []
    }
  },
  computed: {
    publishOption () {
      return this.$store.state.news.data
    }
  },
  watch: {
  },
  mounted () {
    this._getNewsCategory()
    // 将表单挂载到全局以供在保存时检查表单使用
    this.$nuxt.__checkFormPublish = this.$refs.publishForm
  },
  methods: {
    /**
     * 当表单修改时，更新vuex state
     */
    setPublishOption () {
      this.$store.commit('news/setNewsData', this.publishOption)
    },
    /**
     * 递归判断列表，把最后的children设为null
     * @param {*} data
     */
    _getTreeData (data) {
      for (let i = 0; i < data.length; i++) {
        if (data[i].children.length < 1) {
          // children若为空数组，则将 children 设为null
          data[i].children = null
        } else {
          // children若不为空数组，则继续 递归调用 本方法
          this._getTreeData(data[i].children)
        }
      }
      return data
    },
    _getNewsCategory () {
      // 请求文章分类
      this.$axios.get('/api/system/category/tree/?menuType=2')
        .then(({ data }) => {
          if (data.code === 200) {
            this.categoryOptions = this._getTreeData(data.data)
          }
        })
    }
  }
}
